/** ================Works================ */

.works{
    padding: 80px 0;
}
.works__container{
    padding: 0 20px;
}
.works__title{
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 34px;
    text-align: center;
    color: var(--main-color);

    margin-bottom: 40px;
}
.works__box{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 25px;
    column-gap: 20px;
}
.works__card{
    width: 320px;
    height: 320px;
}
.card-overlay{
    position: relative;
    background: linear-gradient(rgba(0,0,0,0.9),rgba(0,0,0,0.9)),url(../../assets/works.jpg);
}
.works__card-title{
    margin-top: 30px;
    margin-bottom: 16px;

    text-align: center;
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 34px;
    color: var(--main-color);
}
.works__card-text{
    max-width: 219px;
    margin: 0 auto;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 17px;
    text-align: center;
    position: relative;

    margin-bottom: 30px;
}
.works__card-line::after{
    content: '';
    position: absolute;
    left: 0;
    bottom: -15px;
    display: block;
    background-color: var(--main-color);
    width: 200px;
    height: 2px;
    margin-top: 30px;
}
.works__btn{
    text-align: center;
}
.works__card-btn{
    background: #32312E;
    border-radius: 4px;
    padding: 15px 30px;

    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 15px;
}

/** ================Works mobile================ */

@media(max-width:400px) {
    .works__card{
        width: 300px;
        height: 300px;
    }
}
@media(max-width:300px) {
    .works__card-title{
        font-size: 25px;
    }
    .works__card-text{
        font-size: 14px;
    }
}