/** ================Header================ */

.header{
    /* min-height: 720px; */
}

.header__container{
    max-width: 1334px;
    padding: 0 50px;
}
.header__wrapper{
    display: flex;
    justify-content: space-between;
}
.header__content{
    padding-top: 220px;
}
.header__img{
    padding-top: 120px;
}
.header__title{
    font-style: normal;
    font-weight: 900;
    font-size: 64px;
    line-height: 67px;
    color: var(--main-color);
    
    margin-bottom: 10px;
}
.header__subtitle{
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 42px;

    margin-bottom: 10px;
}
.header__text{
    max-width: 448px;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 21px;
}

/** ================Header mobile================ */
@media(max-width:992px) {
    .header{
        min-height: 88vh;
    }
    .header__wrapper{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .header__content{
        padding-top: 130px;
    }
}
@media(max-width:500px) {
    .header__container{
        padding: 0 20px;
    }
    .header__title{
        font-size: 45px;
        margin: 0;
    }
    .header__subtitle{
        font-size: 25px;
    }
    .header__text{
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 25px;
    }
}
@media(max-width:365px) {
    .header__title{
        font-size: 35px;
    }
    .header__subtitle{
        font-size: 25px;
    }
}
@media(max-width:305px) {
    .header__container{
        padding: 0 8px;
    }
} 