/** ================About================ */

.about{
    padding: 80px 0;
}
.about__container{
    max-width: 1036px;
    margin: 0 auto;
}
.about__content{
    display: flex;
    align-items: center;
    column-gap: 30px;
}
.about__text{
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 25px;
}

/** ================About mobile================ */

@media(max-width:768px) {
    .about__container{
        padding: 0 30px;
    }
    .about__text{
        max-width: 443px;
    }
}
@media(max-width:630px) {
    .about__text{
        max-width: 400px;
    }
} 
@media(max-width:600px) {
    .about__content{
        flex-direction: column;
        row-gap: 32px;
    }
}
@media(max-width:450px) {
    .about__container{
        padding: 0 20px;
    }
    .about__text{
        font-size: 21px;
        text-align: center;
    }
}