/** ================Footer================ */

.footer{
    background-color: #1F1E1B;
    padding: 40px 0;
}
.footer__container{
    max-width: 661px;
    margin: 0 auto;
}
.footer__wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.footer__info svg{
    margin-bottom: 30px;
}
.footer__info-text{
    max-width: 213px;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 21px;
}
.footer__contact-title{
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 34px;
    color: var(--main-color);

    margin-bottom: 30px;
}
.contact__box{
    display: flex;
    column-gap: 30px;
}
.contact__box-link{
    display: flex;
    align-items: center;
    column-gap: 16px;
    color: var(--main-color);
    padding-bottom: 30px;
}
.contact__box-link:hover{
    text-decoration: underline;
}

/** ================Footer mobile================ */

@media(max-width:768px) {
    .footer__wrapper{
        flex-direction: column;
    }
    .footer__info{
        margin-bottom: 40px;
    }
    .footer__info svg{
        display: block;
        margin: 0 auto;
        margin-bottom: 40px;
    }
    .footer__contact-title{
        text-align: center;
    }
}
@media(max-width:450px) {
    .footer__info-text{
        max-width: 339px;
    }
    .footer__contact{
        flex-direction: column;
    }
    .contact__box {
        flex-direction: column;
    }
}
@media(max-width:400px) {
    .footer__info-text{
        font-size: 18px;
        max-width: 300px;
    }
}
@media(max-width:320px) {
    .footer__info-text{
        max-width: 250px;
        text-align: center;
    }
}