/** ================Skills================ */

.skills{
    padding: 80px 0;
}
.skills__title{
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 34px;
    text-align: center;
    color: var(--main-color);

    margin-bottom: 40px;
}
.skills__box{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    column-gap: 20px;
}
.skills__card{
    background-color: #21201D;
    border-radius: 10px;
    width: 200px;
    height: 300px;
    padding: 20px;
    text-align: center;
}
.skills__card svg{
    margin-top: 30px;
    margin-bottom: 20px;
}
.skills__card h3{
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    color: #fff;
}

/** ================Skills mobile================ */
@media(max-width:1100px) {
    .skills__box{
        row-gap: 26px;
    }
}
@media(max-width:600px) {
    .skills__box{
        flex-direction: column;
        row-gap: 26px;
    }
    .skills__card{
        width: 285px;
        height: 60px;
        display: flex;
        align-items: center;
        column-gap: 16px;
    }
    .skills__card h3{
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 25px;
    }
    .skills__card svg{
        width: 32px;
        height: 32px;
    }
}
@media(max-width:330px) {
    .skills__card{
        width: 250px;
    }
}