/** ================Navbar================ */

.nav{
    position: fixed;
    width: 100%;
    z-index: 10;
    padding: 29px 0;
}
.nav__container{
    max-width: 1200px;
    padding: 0 50px;
}
.nav__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.nav__list{
    display: flex;
    column-gap: 30px;
}
.nav__item-link{
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 21px;
    text-transform: capitalize;
}
.nav__item-link:hover{
    color: var(--main-color);
    transition: .2s all ease;
}
.nav__burger{
    display: none;
}

/** ================Navbar mobile================ */

@media(max-width:850px) {
    .nav__container{
        padding: 0 30px;
    }
}
@media(max-width:768px) {
    .nav__wrapper{
        flex-direction: column;
    }
    .nav__logo{
        margin-bottom: 16px;
    }
}
@media(max-width:500px) {
    .nav__wrapper{
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
    .nav__list{
        display: flex;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        position: fixed;
        left: 0;
        right: 0;
        top: -100%;
        bottom: 0;
        row-gap: 25px;
        width: 100%;
        height: 100vh;
        background-color: #1B1A17;
        z-index: 100;
        transition: .2s all ease-in;
        overflow: hidden;
    }
    .nav__active{
        top: 0;
    }
    .nav__item-link{
        font-size: 30px;
        color: #F0E3CA;
    }
    .nav__burger{
        display: block;
        position: absolute;
        z-index: 1001;
        right: 8px;
        top: 25px;
        cursor: pointer;
        overflow: hidden;
    }
    .nav__logo{
        margin: 0;
        position: absolute;
        z-index: 1001;
        margin-top: 20px;
        overflow: hidden;
    }
}
@media(max-width:450px) {
    .nav__container{
        padding: 0 8px;
    }
}